.backdrop {
  width: 100%;
  height: 100%;
  z-index: 25;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .2s ease-in;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--active {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity .2s ease-in;
}

.backdrop--active .modal {
  transform: scale(.9);
}

.modal {
  width: 280px;
  max-height: 90%;
  visibility: visible;
  opacity: 1;
  background-color: var(--white-color);
  box-shadow: var(--accent-color) 1px 1px 50px, var(--accent-color) 1px 1px 100px;
  box-shadow: 0px 0px 80px 15px var(--accent-color);
  border-radius: 25px;
  transition: opacity .25s ease-in, visibility .25s ease-in, transform .25s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .modal {
    width: 704px;
    height: auto;
  }
}

@media screen and (min-width: 1024px) {
