    height: auto;
  }
}

.slideDownIn {
  animation-name: slideDownIn;
  animation-duration: .8s;
  animation-timing-function: ease;
}

@keyframes slideDownIn {
  0% {
    scale: 0;
  }

  100% {
    scale: 1;
  }
}

